.category-menu {
  padding: 32px 0;
  display: flex;
  text-align: center;
  justify-content: center;
}

.category-tabs {
  background: #f0f0f0;
  border-radius: 30px;
  padding: 8px;
  align-items: center;
  text-align: center;
  display: inline-flex;
  gap: 8px;
}

.category-tab {
  padding: 12px 24px;
  border: none;
  border-radius: 30px;
  font-weight: 500;
  font-size: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  background: transparent;
  color: #000;
}

.category-tab.active {
  background: #fdd500;
  color: #000;
}

.category-tab:hover:not(.active) {
  background: rgba(253, 213, 0, 0.3);
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .category-menu {
    padding: 24px 0;
  }

  .category-tabs {
    padding: 6px;
    gap: 4px;
  }

  .category-tab {
    padding: 10px 16px;
    font-size: 16px;
  }
}

@media (max-width: 480px) {
  .category-menu {
    padding: 20px 0;
  }

  .category-tab {
    padding: 8px 12px;
    font-size: 14px;
  }
}
